@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ephesis&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

@media screen and (min-width: 768px) {
    :root {
        --font-family: "Poppins", serif;
        --global-color: #d9d9d9;
        --global-dark: #1a1a1a;
        --bg: #d9d9d9;
        --second-font: "Ephesis";
        --white: #fff;
        --font-size: 16px;
        --font-rem: 16rem;
        --font-rem2: 6rem;
        --font-rem3: 1rem;
        --font-rem4: 8rem;
        --font-rem5: 12rem;
        --margin-px1: -480px;
        --margin-px2: -320px;
        --margin-px3: 480px;
    }
}

@media screen and (max-width: 768px) {
    :root {
        --font-family: "Poppins", serif;
        --global-color: #d9d9d9;
        --bg: #d9d9d9;
        --second-font: "Ephesis";
        --white: #fff;
        --font-size: 4px;
        --font-rem: 16rem;
        --font-rem2: 11rem;
        --font-rem3: 4rem;
        --font-rem4: 12rem;
        --font-rem5: 16rem;
        --margin-px1: -40px;
        --margin-px2: -40px;
        --margin-px3: 40px;
    }
}

html {
    font-size: var(--font-size);
}

html,
body {
    margin: 0;
    padding: 0;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font-family);
    color: var(--global-dark);
    font-smooth: always;
    -webkit-font-smoothing: antialiased !important;
    line-height: 1;
}
.NavContainer {
    width: 100%;
    padding: 30px 48px;
    display: flex;
    flex-direction: row;
    position: fixed;
    top: 0;
    left: 0;
    color: var(--global-color);
    justify-content: space-between;
    align-items: center;
    background: #ffffff00;
    mix-blend-mode: difference;
    z-index: 10000;
}
.Nav li {
    display: inline;
    font-size: 0.95rem;
    font-weight: 600;
    color: var(--global-color);
}

.Brand {
    height: 48px;
    padding: 0;
    margin: 0;
    mix-blend-mode: difference;
}

.NavIcon {
    width: 120px;
    height: 20px;
    border-bottom: 2px solid var(--bg);
    border-top: 2px solid var(--bg);
    color: #fff;
}

.HomePage {
    padding: 0;
    margin: 0;
}

.Container {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    position: relative;
}

.Section {
    width: 100%;
    height: 100vh;
}

.Hero {
    background: url("/src/Assets/hero.jpg");
    background-size: cover;
    background-position: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-attachment: fixed;
}

.HeroHeader {
    color: var(--global-color);
    font-size: 17rem;
    font-weight: 600;
    line-height: 1;
    letter-spacing: 0px;
}
.HeroSub {
    font-size: var(--font-rem2);
    line-height: 0.3;
    display: flex;
    font-weight: 100;
    font-family: var(--second-font);
    color: var(--global-color);
}

.HeroBottom {
    position: absolute;
    bottom: 16%;
    text-transform: uppercase;
    text-align: center;
    font-weight: 500;
    font-size: var(--font-rem3);
    color: var(--global-color);
}

.About {
    background: var(--bg);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}

.AboutRow {
    font-size: var(--font-rem4);
    line-height: 0.85em;
}

.AboutRow1,
.AboutRow3,
.AboutRow4 {
    text-transform: uppercase;
    font-weight: 500;
}

.AboutRow2,
.AboutRow5 {
    font-family: var(--second-font);
}

.AboutRow1,
.AboutRow4 {
    margin-left: var(--margin-px1);
}
.AboutRow2 {
    margin-left: var(--margin-px2);
}
.AboutRow3,
.AboutRow5 {
    margin-left: var(--margin-px3);
}
